@import "./bootstrap/variables.less";

// SnapFast1.0 Custom Less

.fw-300 {
  font-weight: 300;
}

.hero-img {
  padding: 4em 0 0 4em;
}

.form-control, output {
  font-size: 18px !important;
}

.form-control {
  border-radius: 2px !important;
}

.hero-btn {
  width: 100%;
  border-radius: 2px !important;
}

.app-price-plans {
  background: #f4f5f6;
}

.brand-primary {
  color: @brand-primary;
}

.navbar-right {
  margin-top: 0px !important;
}


.browser-content {
  height: 550px;
  overflow: hidden;
  position: relative;
  background-image: url("../docs/assets/img/load-speed.gif");
  background-position: top center;
  background-size: cover;
}

// Section 1

.happily-married {
  height: 420px;
  margin: 0 0 80px 28px;
}

// Media Queries
// Mobile Only
// Hero
  .navbar-brand {
    float: none !important;
    @media (min-width: @screen-sm-min) {
      float: left;
  }}

  .logo-header {
  margin: auto;
  text-align: center;
  @media (min-width: @screen-sm-min) {
    margin: none;
  text-align: left;
  }}

#hero {
  form {
    width: 72%;
    text-align: center;
    margin: auto;
    @media (min-width: @screen-sm-min) {
      width: 100%;
      text-align: center;
      margin: auto;
    }
  }

  h1,
  h4 {
    text-align: center !important;
    @media (min-width: @screen-sm-min) {
      text-align: left !important;
  }}



  div,
  .btn-caption {
    text-align: center !important;
    @media (min-width: @screen-sm-min) {
      text-align: left !important;
  }}
}



//Footer
.pricing-box {
  padding: 42px !important;
  @media (min-width: @screen-sm-min) {
    padding: 20px !important;
}}

.list-features {
  text-align: center;
  @media (min-width: @screen-sm-min) {
    text-align: left;
}}

.price-head-row {
  margin: 60px auto 0;
  @media (min-width: @screen-sm-min) {
    margin: 60px auto;
  }
}

.pricing-head {
  width: 72%;
  text-align: center;
  margin: auto;
  @media (min-width: @screen-sm-min) {
    width: inherit;
    text-align: inherit;
    margin: inherit;
  }
}

.narrow {
  margin: 20px 20px 40px 20px;
  @media (min-width: @screen-sm-min) {
    margin: 26px 80px 40px 80px;
  }
}




